<template>
    <div id="app">
        <el-row style="height: 100%;">
            <el-col :sm="4" style="background: rgb(50, 65, 87);height: 100%;">
                <el-menu :router="true" :unique-opened=true default-active="2" class="el-menu-vertical-demo"
                         theme="dark">
                    <el-submenu index="user">
                        <template slot="title"></template>
                        <el-menu-item index="/user/list"></el-menu-item>
                        <el-menu-item index="/user/confirm"></el-menu-item>
                        <el-menu-item index="/user/account"></el-menu-item>
                    </el-submenu>
                    <el-submenu index="task">
                        <template slot="title"></template>
                        <el-menu-item index="/task/recommend"></el-menu-item>
                        <el-menu-item index="/task/report"></el-menu-item>
                        <!--<el-menu-item index="/task/watch"></el-menu-item>-->
                    </el-submenu>
                    <el-submenu index="topic">
                        <template slot="title"></template>
                        <el-menu-item index="/topic/recommend"></el-menu-item>
                        <el-menu-item index="/topic/report"></el-menu-item>
                        <!--<el-menu-item index="/topic/watch"></el-menu-item>-->
                    </el-submenu>
                    <el-submenu index="sys">
                        <template slot="title"></template>
                        <el-menu-item index="/sys/swiper"></el-menu-item>
                        <el-menu-item index="/sys/friend"></el-menu-item>
                        <el-menu-item index="/sys/ad"></el-menu-item>
                    </el-submenu>
                    <el-menu-item index="/dispute"></el-menu-item>
                    <el-menu-item index="/credit"></el-menu-item>
                    <el-menu-item index="/message"></el-menu-item>
                </el-menu>
            </el-col>
            <el-col :sm="20" style="overflow-y: scroll;">
                <div style="text-align: right;margin-right: 30px;" v-if="userName">
                    <i class="el-icon-setting"></i>
                    <span class="sys-name">{{userName}}</span>
                    <el-button type="text" @click="onLoyout"></el-button>
                </div>
                <router-view></router-view>
            </el-col>
        </el-row>
        <MyFooter/>

    </div>
</template>

<script>
  import MyFooter from './components/my_footer.vue'
  export default {
    name: 'app',
    data(){
      return {
        userName:'-timebank'
      }
    },
    computed: {},
    methods: {
      onLoyout(){
        let self = this
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          self.userName = ''
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    },
    mounted() {
    },
    components: {
      MyFooter

    }
  }
</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        /*text-align: center;*/
        color: #2c3e50;
        /*background: #abcabc;*/
        height: 100%;
    }
</style>



