<template>
    <wrap title="">
        <div slot="content">
            <el-tabs type="border-card">
                <el-tab-pane>
                    <span slot="label"><i class="el-icon-date"></i> </span>
                    <!--<div style="width: 500px;">-->
                    <el-form ref="rechargeForm" :model="rechargeForm" :rules="rechargeFormRules" label-width="150px">
                        <el-form-item label="ID" prop="userId">
                            <el-input v-model="rechargeForm.userId"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="count">
                            <el-input-number v-model="rechargeForm.count"></el-input-number>

                        </el-form-item>
                        <el-form-item label="" prop="result">
                            <el-input v-model="rechargeForm.result"></el-input>
                        </el-form-item>

                        <el-form-item>
                            <el-checkbox-group v-model="rechargeForm.check">
                                <el-checkbox label=""></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>

                    </el-form>
                    <div style="text-align: center;">
                        <el-button type="primary" @click.stop="onCharge"></el-button>
                    </div>
                    <!--</div>-->
                </el-tab-pane>

                <el-tab-pane>
                    <span slot="label"><i class="el-icon-date"></i> </span>
                    <el-form ref="deductForm" :rules="deductFormRules" :model="deductForm" label-width="150px">
                        <el-form-item label="ID" prop="userId">
                            <el-input v-model="deductForm.userId"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="count">
                            <el-input-number v-model="rechargeForm.count"></el-input-number>

                        </el-form-item>
                        <el-form-item label="" prop="result">
                            <el-input v-model="deductForm.result"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-checkbox-group v-model="deductForm.check">
                                <el-checkbox label=""></el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>

                    </el-form>
                    <div style="text-align: center;">
                        <el-button type="primary" @click.stop="onDeduct"></el-button>
                    </div>
                </el-tab-pane>
                <el-tab-pane>
                    <span slot="label"><i class="el-icon-date"></i> </span>
                    <el-table
                            align="center"
                            :data="deductData"
                            stripe
                            style="width: 100%">
                        <el-table-column type="expand">
                            <template scope="props">
                                <p>{{props.row.result}}</p>
                            </template>
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="userId"
                                label="ID"
                                width="80">
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="name"
                                label=""
                                width="180">
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="type"
                                label=""
                        >
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="number"
                                label=""
                        ></el-table-column>
                        <el-table-column
                                align="center"
                                prop="createTime"
                                label=""
                                width="150"
                        >
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="admin"
                                label="">
                        </el-table-column>
                        <el-table-column
                                align="center"
                                prop="status"
                                label=""
                        >
                        </el-table-column>

                    </el-table>
                </el-tab-pane>

            </el-tabs>
        </div>
    </wrap>
</template>
<script>
  import Wrap from '@/components/wrap'
  export default{
    name: 'credit',
    data(){
      return {
        rechargeFormRules:{
          userId: {
            required: true,
            validator: this.checkUserId,
            trigger: 'blur'
          },
          count: {
            required: true,
            validator: this.checkMoney,
            trigger: 'blur,change'
          },
          result: {required: true, message: '', trigger: 'blur'},

        },
        deductFormRules:{
          userId: {
            required: true,
            validator: this.checkUserId,
            trigger: 'blur'
          },
          count: {
            required: true,
            validator: this.checkMoney,
            trigger: 'blur,change'
          },
          result: {required: true, message: '', trigger: 'blur'},

        },

        rechargeForm:{
          userId: this.$route.query.userId || '',
          count:'1',
          result:'',
          message:'XXX  XX ' +
          '13505618325!',
          check:''
        },
        deductForm:{
          userId: this.$route.query.userId || '',
          count:'1',
          result:'',
          message:'XXX  XX ' +
          '13505618325',
          check:''
        },
        deductData:[
          {
            userId:1,
            name:'',
            type:'',
            number:'20',
            result:'',
            createTime:'2017-05-02 12:33',
            admin:'timebank',
            status:'',
          },

        ]
      }
    },
    components: {
      Wrap
    }
  }
</script>


