<template>
    <div>
        <wrap title="">
            <div slot="content">
                <el-table
                        :data="reportData"
                        stripe
                        style="width: 100%">
                    <el-table-column type="expand">
                        <template scope="props">
                            <p>{{props.row.admin}}</p>
                            <p>{{props.row.result}}</p>
                            <p>{{props.row.reason}}</p>
                            <p>{{props.row.createTime}}</p>
                        </template>
                    </el-table-column>
                    <el-table-column
                            align="center"
                            type="index"
                            label=""
                            width="80">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="topicId"
                            label="ID"
                            width="80">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="userName"
                            label=""
                            width="120">
                    </el-table-column>

                    <el-table-column
                            align="center"
                            prop="title"
                            label=""
                            width="300"
                    >
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="reportCount"
                            label=""
                            width="120"
                    >
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="status"
                            label=""
                            width="100"
                            :formatter="formatStatus"
                    >
                    </el-table-column>
                    <el-table-column label="" align="center">
                        <template scope="props">
                            <el-button type="text"></el-button>
                            <el-button type="text" @click="reportFormVisible = true"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-dialog title="" v-model="reportFormVisible">
                    <el-form :model="reportForm">
                        <el-form-item label="">
                            <el-select v-model="reportForm.result" placeholder="">
                                <el-option label="" value="0"></el-option>
                                <el-option label="" value="1"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input type="textarea" v-model="reportForm.reason"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="reportFormVisible = false"> </el-button>
                        <el-button type="primary" @click="reportFormVisible = false"> </el-button>
                    </div>
                </el-dialog>
            </div>
        </wrap>
    </div>
</template>
<script>
  import Wrap from '@/components/wrap'
  export default{
    name:'ReportTopic',
    data(){
      return {
        reportData: [
          {
            topicId: 1,
            userName: 'lala',
            title: '',
            reportCount: 3,
            status: 0,
            admin: 'timebank',
            createTime: "2017-5-10 12:00",
            reason: '',
            result: ''

          },

        ],
        reportFormVisible: false,
        reportForm: {
          result:'',
          reason:''
        }

      }
    },
    methods:{
      formatStatus(row){
        switch (row.userType) {
          case 0:
            return ''
          case 1:
            return ''
          default:
            return ''
        }
      }
    },
    components: {
      Wrap
    }
  }
</script>


