<template>
    <div>
        <wrap title="">
            <div slot="content">
                <el-table
                        :data="reportData"
                        stripe
                        style="width: 100%">
                    <el-table-column type="expand">
                        <template scope="props">
                            <p>ID{{props.row.reportId}}</p>
                            <p>{{props.row.reportName}}</p>
                            <p>{{props.row.detail}}</p>
                            <!--<p>{{props.row.admin}}</p>
                            <p>{{props.row.result}}</p>
                            <p>{{props.row.createTime}}</p>-->
                        </template>
                    </el-table-column>
                    <el-table-column
                            align="center"
                            type="index"
                            label=""
                            width="80">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="disputeId"
                            label="ID"
                            width="80">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="userName"
                            label=""
                            width="120">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="phone"
                            label=""
                            width="150"
                    >
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="beReportName"
                            label=""
                            width="120"
                    >
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="status"
                            label=""
                            width="100"
                            :formatter="formatStatus"
                    >
                    </el-table-column>
                    <el-table-column label="" align="center">
                        <template scope="props">
                            <el-button type="text"></el-button>
                            <el-button type="text" @click="reportFormVisible = true"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <el-dialog title="" v-model="reportFormVisible">
                    <el-form :model="reportForm">
                        <el-form-item label="">
                            <el-input type="textarea" v-model="reportForm.reason"></el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button @click="reportFormVisible = false"> </el-button>
                        <el-button type="primary" @click="reportFormVisible = false"> </el-button>
                    </div>
                </el-dialog>
            </div>
        </wrap>
    </div>
</template>
<script>
  import Wrap from '@/components/wrap'
  export default{
    name:'UserDispute',
    data(){
      return {
        reportData: [
          {
            reportId:1,
            reportName:'',
            detail:'',
            disputeId: 1,
            userName: 'lala',
            phone:'13505618325',
            beReportName:'long',
            status: 0,

            admin: 'timebank',
            createTime: "2017-5-10 12:00",
            result: ''

          },

        ],
        reportFormVisible: false,
        reportForm: {
          result:'',
          reason:''
        }

      }
    },
    methods:{
      formatStatus(row){
        switch (row.userType) {
          case 0:
            return ''
          case 1:
            return ''
          default:
            return ''
        }
      }
    },
    components: {
      Wrap
    }
  }
</script>


