                    <el-button type="primary" @click="recommendFormVisible = true"></el-button>
                </div>
                <el-table
                        :data="recommendData"
                        stripe
                        style="width: 100%">
                    <el-table-column
                            align="center"
                            type="index"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column
                            align="center"
                            prop="topicId"
                            label="ID"
