const MyPlugin = {}
MyPlugin.install = function(Vue, options) {
  // 
  /* Vue.prototype.$GM = {
   fn1(){
   console.log(" $GPP.fn() ", this, Vue.prototype );
   },
   routerPush(data){
   console.log(" $GPP.fn() ", this, Vue.prototype );
   console.info( router );
   }
   } */

  Vue.prototype.GM_routerPush = function(ops) {
    console.debug('-GM_routerPush', ops)
    // console.log(" $GPP.fn() ", this.$router, Vue.prototype );
    let config = {}

    if (ops.hasOwnProperty('path')) {
      config.path = ops.path
    }

    if (ops.hasOwnProperty('query')) {
      config.query = {
        ...this.$route.query,
        ...ops.query,
        _: Date.now().toString() //  url 
      }
    }

    this.$router.push(config)
  }
}

export default MyPlugin



