<template>
    <div>
        <wrap title="">
            <div slot="content">
                <el-tabs type="border-card">
                    <el-tab-pane>
                        <span slot="label"><i class="el-icon-date"></i> </span>
                        <!--<div style="width: 500px;">-->
                            <el-form ref="rechargeForm" :model="rechargeForm" :rules="rechargeFormRules" label-width="130px">
                                <el-form-item label="ID" prop="userId">
                                    <el-input v-model="rechargeForm.userId"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="count">
                                    <el-input-number v-model="rechargeForm.count"></el-input-number>

                                </el-form-item>
                                <el-form-item label="" prop="result">
                                    <el-input v-model="rechargeForm.result"></el-input>
                                </el-form-item>

                                <el-form-item>
                                    <el-checkbox-group v-model="rechargeForm.check">
                                        <el-checkbox label=""></el-checkbox>
                                    </el-checkbox-group>
                                </el-form-item>

                            </el-form>
                            <div style="text-align: center;">
                                <el-button type="primary" @click.stop="onCharge"></el-button>
                            </div>
                        <!--</div>-->
                    </el-tab-pane>

                    <el-tab-pane>
                        <span slot="label"><i class="el-icon-date"></i> </span>
                        <el-form ref="deductForm" :rules="deductFormRules" :model="deductForm" label-width="130px">
                            <el-form-item label="ID" prop="userId">
                                <el-input v-model="deductForm.userId"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="count">
                                <el-input v-model="deductForm.count"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="result">
                                <el-input v-model="deductForm.result"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-checkbox-group v-model="deductForm.check">
                                    <el-checkbox label=""></el-checkbox>
                                </el-checkbox-group>
                            </el-form-item>

                        </el-form>
                        <div style="text-align: center;">
                            <el-button type="primary" @click.stop="onDeduct"></el-button>
                        </div>
                      </el-tab-pane>
                    <el-tab-pane>
                        <span slot="label"><i class="el-icon-date"></i> </span>
                        <el-table
                                :data="deductData"
                                stripe
                                style="width: 100%">
                            <el-table-column type="expand">
                                <template scope="props">
                                    <p>{{props.row.result}}</p>
                                </template>
                            </el-table-column>
                            <el-table-column
                                    prop="userId"
                                    label="ID"
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    prop="name"
                                    label=""
                                    width="180">
                            </el-table-column>
                            <el-table-column
                                    prop="type"
                                    label=""
                                    :formatter="formatType"
                            >
                            </el-table-column>
                            <el-table-column
                                    prop="createTime"
                                    label="">
                            </el-table-column>
                            <el-table-column
                                    prop="admin"
                                    label="">
                            </el-table-column>
                            <el-table-column
                                    prop="status"
                                    label=""
                                    :formatter="formatStatus"
                            >
                            </el-table-column>

                        </el-table>
                      </el-tab-pane>

                </el-tabs>
            </div>
        </wrap>
    </div>
</template>
<script>
  import Wrap from '@/components/wrap'
  import {mapActions,mapState} from  'vuex'
  import * as AccountType from  '@/store/account/types'
  export default{
    name: 'Account',
    data(){
      return {
        rechargeFormRules:{
          userId: {
            required: true,
            validator: this.checkUserId,
            trigger: 'blur'
          },
          count: {
            required: true,
            validator: this.checkMoney,
            trigger: 'blur,change'
          },
          result: {required: true, message: '', trigger: 'blur'},

        },
        deductFormRules:{
          userId: {
            required: true,
            validator: this.checkUserId,
            trigger: 'blur'
          },
          count: {
            required: true,
            validator: this.checkMoney,
            trigger: 'blur,change'
          },
          result: {required: true, message: '', trigger: 'blur'},

        },

        rechargeForm:{
          userId: this.$route.query.userId || '',
          count:'1',
          result:'',
          message:'XXX  XX ' +
          '13505618325!',
          check:''
        },
        deductForm:{
          userId: this.$route.query.userId || '',
          count:'1',
          result:'',
          message:'XXX  XX ' +
          '13505618325',
          check:''
        },
        deductData:[
          {
            userId:1,
            name:'',
            type:0,
            result:'',
            createTime:'2017-5-2 12:00',
            admin:'timebank',
            status:0,
          }

        ]
      }
    },
    computed:{
      ...mapState({
        chargeStep:state => state.account.chargeStep,
        chargeError:state => state.account.chargeError,
        deductStep:state => state.account.deductStep,
        deductError:state => state.account.deductError,
      })
    },
    methods:{
      ...mapActions({
        chargeMoney:AccountType.A_CHARGE_MONEY,
        deductMoney:AccountType.A_DEDUCT_MONEY,
      }),
      formatStatus(row) {
        switch (row.status) {
          case 0:
            return ''
          case 1:
            return ''
          default:
            return ''
        }
      },
      formatType(row) {
        switch (row.status) {
          case 0:
            return ''
          case 1:
            return ''
          default:
            return ''
        }
      },
      onCharge(){
        let self = this
        self.$refs.rechargeForm.validate(value => {
          if(value && self.rechargeForm.check){
            console.log("")
            this.chargeMoney({
              userId:parseInt(self.rechargeForm.userId),
              money:parseInt(self.rechargeForm.count),
              remark:self.rechargeForm.result,
              type:1
            }).then(()=>{
              if(self.chargeStep === 'error'){
                self.$message.error(self.chargeError)
              }else {
                self.$message.success('')
              }
            })
          }else{
            self.$message.warning('')
            console.log("")
          }
        })
      },
      onDeduct(){
        let self = this
        self.$refs.deductForm.validate(value => {
          if(value && self.deductForm.check){
            this.deductMoney({
              userId:parseInt(self.deductForm.userId),
              money:parseInt(self.deductForm.count),
              remark:self.deductForm.result,
              type:2
            }).then(()=>{
              if(self.deductStep === 'error'){
                self.$message.error(self.deductError)
              }else {
                self.$message.success('')
              }
            })
          }else{
            self.$message.warning('')
            console.log("")
          }
        })
      },

      checkMoney(rule, value, callback){
        if (parseInt(value) < 1) {
          return callback(new Error('1'))
        }
        return callback()
      },
      checkUserId(rule, value, callback){
        console.log("ffd",parseInt(value))
        if (!Number.isInteger(parseInt(value)) || parseInt(value) < 1) {
          return callback(new Error('ID'))
        }
        return callback()
      },

    },
    components: {
      Wrap
    }
  }
</script>


